.countdown-container {
  padding: 20px;
  background-color: #222222;
  margin-bottom: 20px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
}

.countdown-container .number {
  font-size: 32px;
  text-align: center;
  padding-top: 20px;
  color: #ffffff;
  background-color: #000000;
  height: 90px;
}

.countdown-container .concept {
  font-size: 28px;
  text-align: center;
  color: #000000;
}
